<template>
	<div>
		<div class="mask" v-if="show"></div>
		<div class="mask_divbg" v-if="show">
			<div class="mask_div">
				<div class="mask_title_div clearfix">
					<div class="mask_title" v-show="addtitle">添加机构</div>
					<div class="mask_title" v-show="modifytitle">修改机构</div>
					<div class="mask_anniu">
						<span class="mask_span mask_max" @click='toggle'>
							<i v-bind:class="{ 'icon-maximization': isok1, 'icon-restore':isok2}"></i>
						</span>
						<span class="mask_span" @click='close'>
							<i class="icon-close1"></i>
						</span>
					</div>
				</div>
				<div class="mask_content">
					<el-form :model="adddeptForm" :rules="rules" ref="adddeptForm" label-width="100px" id="demo-adduserForm">
						<div class="accordion">
							<el-collapse v-model="activeNames">
								<el-collapse-item title="机构信息" name="1">
									<el-row :gutter="30">
										<el-col :span="4" class="pull-right pb10" >
											<el-input v-model="adddeptForm.version" :disabled="edit">
												<template slot="prepend">版本</template>
											</el-input>
										</el-col>
										<el-col :span="4" class="pull-right" v-if="modify" style="display: none;">
												<el-input v-model="adddeptForm.status==1?'活动':'不活动'" :disabled="edit" >
													<template slot="prepend">信息状态</template>
												</el-input>
										</el-col>
										<el-col :span="4" class="pull-right" v-else style="display: none;">
												<el-input v-model="adddeptForm.status" :disabled="edit" >
													<template slot="prepend">信息状态</template>
												</el-input>
										</el-col>
									</el-row>
									<el-row :gutter="30">
										<el-col :span="8">
											<el-form-item label="机构序号" prop="step">
												<el-input  v-model="adddeptForm.step">
												</el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="机构编码" prop="code">
												<el-input v-model="adddeptForm.code":disabled="edit">
												</el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="机构名称" prop="fullname">
												<el-input v-model="adddeptForm.fullname">
												</el-input>
											</el-form-item>
										</el-col>
									</el-row>
									<el-row :gutter="30">
										<el-col :span="8">
											<el-form-item label="上级机构" prop="parent">
												<el-input v-model="adddeptForm.parent" :disabled="edit">
													<el-button slot="append" icon="el-icon-search" @click="getDept"></el-button>
												</el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="机构类型" prop="depttype">
												<el-select v-model="adddeptForm.depttype" placeholder="请选择" style="width: 100%">
													<el-option v-for="(data,index) in Selectsys_depttype" :key="index" :value="data.code" :label="data.name"></el-option>
												</el-select>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="机构属性" prop="type">
												<el-select v-model="adddeptForm.type" placeholder="请选择" style="width: 100%">
													<el-option v-for="(data,index) in SelectDEPT_TYPE" :key="index" :value="data.code" :label="data.name"></el-option>
												</el-select>
											</el-form-item>
										</el-col>
									</el-row>
									<el-row :gutter="30">
										<el-col :span="8">
											<el-form-item label="是否停用" prop="inactive">
												<el-input v-if="stopcontent" v-model="adddeptForm.inactive" :disabled="edit"></el-input>
												<el-select v-if="stopselect" v-model="adddeptForm.inactive" placeholder="请选择" style="width: 100%">
													<el-option v-for="item in stopoptions" :key="item.value" :label="item.label" :value="item.value">
													</el-option>
												</el-select>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="联系地址">
												<el-input v-model="adddeptForm.address"></el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="邮政编码">
												<el-input v-model="adddeptForm.zipcode">
												</el-input>
											</el-form-item>
										</el-col>
									</el-row>
									<el-row :gutter="30">
										<el-col :span="8" v-show="!addtitle" >
											<el-form-item label="负责人">
												<el-input v-model="adddeptForm.leaderName" :disabled="edit">
													<el-button slot="append" icon="el-icon-search" @click="getPerson"></el-button>
												</el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8"  v-show="addtitle">
											<el-form-item label="负责人">
												<el-input v-model="adddeptForm.leader" :disabled="edit">
													<el-button slot="append" icon="el-icon-search" @click="getPerson"></el-button>
												</el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="电话" prop="telephone">
												<el-input v-model="adddeptForm.telephone"></el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="传真" prop="fax">
												<el-input v-model="adddeptForm.fax">
												</el-input>
											</el-form-item>
										</el-col>
									</el-row>
									<el-row :gutter="30">
										<el-col :span="8">
											<el-form-item label="邮箱" prop="email">
												<el-input v-model="adddeptForm.email"></el-input>
											</el-form-item>
										</el-col>
									</el-row>
									<el-row :gutter="30">
										<el-col :span="24">
											<el-form-item label="备注" prop="tips">
												<el-input :rows="3" type="textarea" v-model="adddeptForm.tips" placeholder="请输入"></el-input>
											</el-form-item>
										</el-col>
									</el-row>
									<el-row :gutter="30" v-show="personinfo">
										<el-col :span="8">
											<el-form-item label="录入人" prop="enterby">
												<el-input v-model="adddeptForm.enterby" :disabled="edit"></el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="录入时间" prop="enterdate">
												<el-input v-model="adddeptForm.enterdate" :disabled="edit">
												</el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="修改人" prop="changeby">
												<el-input v-model="adddeptForm.changeby" :disabled="edit"></el-input>
											</el-form-item>
										</el-col>
										<el-col :span="8">
											<el-form-item label="修改时间" prop="changedate">
												<el-input v-model="adddeptForm.changedate" :disabled="edit">
												</el-input>
											</el-form-item>
										</el-col>
									</el-row>
								</el-collapse-item>
							</el-collapse>
						</div>
						<div class="el-dialog__footer">
							    
							    <el-button type="primary" @click="saveAndUpdate">保存</el-button>
							    <el-button type="success" @click="saveAndSubmit" v-show="addtitle">保存并继续</el-button>
							<!--	<el-button type="primary" class="btn-primarys" @click="submitForm('adddeptForm')">提交</el-button>-->
								<el-button v-if="modify" type="primary" class="btn-primarys" @click="modifyversion">修订</el-button>
								<el-button @click="close">取消</el-button> 
						</div>
					</el-form>
				</div>
			</div>
			<!-- 弹出 -->
			<el-dialog :modal-append-to-body="false" title="机构" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
				<el-tree ref="tree" :data="resourceData" show-checkbox node-key="id" :default-checked-keys="resourceCheckedKey" :props="resourceProps" default-expand-all @node-click="handleNodeClick" @check-change="handleClicks" check-strictly>
				</el-tree>
				<span slot="footer" class="dialog-footer">
			       <el-button @click="dialogVisible = false">取 消</el-button>
			       <el-button type="primary" @click="queding();" >确 定</el-button>
			    </span>
			</el-dialog>

			<!--负责人 Begin-->
			<el-dialog :modal-append-to-body="false" title="选择负责人" :visible.sync="dialogLeader" width="80%" :before-close="handleClose">
				<div class="accordion" id="information">
					<div class="mask_tab-block">
						<!-- <div class="mask_tab-head clearfix">
							<div class="accordion_title">
								<span class="accordion-toggle">选择负责人</span>
							</div>
						</div> -->
						<!-- 第二层弹出的表格 -->
							<el-table :data="userList" border stripe :height="fullHeight" style="width: 100%;" :default-sort="{prop:'userList', order: 'descending'}" @selection-change="SelChange" v-loadmore="loadMore">
									<el-table-column type="selection" width="55" fixed>
									</el-table-column>
									<el-table-column label="账号" sortable width="140px" prop="username">
									</el-table-column>
									<el-table-column label="姓名" sortable width="200px" prop="nickname">
									</el-table-column>
									<el-table-column label="机构" sortable width="150px" prop="deptName">
									</el-table-column>
									<el-table-column label="公司" sortable width="200px" prop="companyName">
									</el-table-column>
									<el-table-column label="信息状态" sortable width="200px" prop="enabled" :formatter="judge">
									</el-table-column>
									<el-table-column label="创建时间" width="200px" prop="createTime" sortable :formatter="dateFormat">
									</el-table-column>
								</el-table>
								<el-pagination background class="pull-right pt10" @size-change="sizeChange" @current-change="currentChange" :current-page="page.currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="page.pageSize" layout="total, sizes, prev, pager, next" :total="page.totalCount">
								</el-pagination>
							<!-- 表格 -->
					</div>
				</div>
				<span slot="footer" class="dialog-footer">
			       <el-button @click="dialogLeader = false">取 消</el-button>
			       <el-button type="primary" @click="addleader">确 定</el-button>
			    </span>
			</el-dialog>
			<!--负责人 End-->
		</div>
	</div>
</template>

<script>
	import Config from '../../config.js'
	// import Validators from '../../core/util/validators.js'
	export default {
		name: 'masks',
		props: {
			adddeptForm: {
				type: Object,
				default: function(){
					return {
						version:'',
						status:'',
						step:'',
						code:'',
						fullname:'',
						parent:'',
						depttype:'',
						type:'',
						inactive:'',
						address:'',
						zipcode:'',
						leader:'',
						telephone:'',
						fax:'',
						email:'',
						tips:'',
						pid:'',
						enterby:'',
						enterdate:'',
						changeby:'',
						changedate:''
					}
				}
			}
		},
		data() {
			return {
				basic_url: Config.dev_url,
				value: '',
				loadSign: true, //加载
				commentArr: {},
				options: [{
					value: '1',
					label: '活动'
				}, {
					value: '2',
					label: '不活动'
				}],

				stopoptions: [{
					value: '1',
					label: '是'
				}, {
					value: '2',
					label: '否'
				}],
				personinfo:false,
				showcode:true,
				selMenu:[],
				selUser: [],
				selData: [],//获取当前负责人
				SelectDEPT_TYPE:[],//获取机构属性
				Selectsys_depttype:[],//获取机构类型
				activeNames: ['1'], //手风琴数量
				userList: [],
				page: {
					currentPage: 1,
					pageSize: 10,
					totalCount: 0
				},
				dialogVisible: false, //对话框
				edit: true, //禁填
				editSearch: '',
				fullHeight: document.documentElement.clientHeight - 210+'px',//获取浏览器高度
				show: false,
				isok1: true,
				isok2: false,
//				labelPosition: 'top',
				addtitle:true,
				modifytitle:false,
				modify:false,
				dialogLeader:false,
				stopcontent:false,
				stopselect:false,
	          	//tree树菜单
				resourceData: [], //数组，我这里是通过接口获取数据
				resourceDialogisShow: false,
				resourceCheckedKey: [], //通过接口获取的需要默认展示的数组 [1,3,15,18,...]
				resourceProps: {
					children: "children",
					label: "fullname"
				},
				rules:{
   					step: [{required:true,trigger: 'blur',validator: this.Validators.isWorknumber}],//机构序号
					fullname: [
						{required:true, trigger: 'blur', message: '必填'},
						{validator: this.Validators.isNickname, trigger: 'blur'},
					],//机构名称
					depttype: [{required:true,trigger: 'change',message: '请选择机构类型'}],//选择机构类型
   					type: [{required:true,trigger: 'change',message: '请选择机构属性'}],//选择机构属性
   					telephone: [{required:false,trigger: 'blur',validator: this.Validators.isTelephone}],//电话
					fax: [{required:false,trigger: 'blur',validator: this.Validators.isTelephone}],//传真
					email:[{required:false, trigger: 'blur', validator: this.Validators.isEmail,}],//邮箱
					code:[{required: false,trigger: 'blur',validator: this.Validators.isWorknumber}],//机构属性
					address:[{required: false,trigger: 'blur',validator: this.Validators.isSpecificKey}],//联系地址
					zipcode:[{required: false,trigger: 'blur',validator: this.Validators.isSpecificKey}],//邮政编码
					telephone:[{required: false,trigger: 'blur',validator: this.Validators.isTelephone}],//电话
					fax:[{required: false,trigger: 'blur',validator: this.Validators.isTelephone}],//传真
					email:[{required: false,trigger: 'blur',validator: this.Validators.isEmail}],//邮箱
					tips:[{required: false,trigger: 'blur',validator: this.Validators.isSpecificKey}],//备注
				}
			}
		},
		methods: {
			handleNodeClick(data) { //获取勾选树菜单节点
				//console.log(data);
			},
			handleClicks(data,checked, indeterminate) {
				this.getCheckboxData = data;
           		 this.i++;
            		if(this.i%2==0){
                	if(checked){
                    	this.$refs.tree.setCheckedNodes([]);
                    	this.$refs.tree.setCheckedNodes([data]);
                    	//交叉点击节点
               		 }else{
                     this.$refs.tree.setCheckedNodes([]);
                    	//点击已经选中的节点，置空
                	 }
            		}
        	},
        	
			visible() {//点击父组件按钮显示弹窗
				this.$axios.get(this.basic_url +'/api-user/users/currentMap', {}).then((res) => {
	     			this.adddeptForm.enterby = res.data.nickname;
	     			var date=new Date();
					this.adddeptForm.enterdate = this.$moment(date).format("YYYY-MM-DD HH:mm:ss");
				 }).catch((err) => {
				 	this.$message({
				 		message: '网络错误，请重试',
				 		type: 'error'
				 	});
				});	
				// this.$refs["adddeptForm"].resetFields();//清空表单验证
				this.show = true;
				this.addtitle = true;
				this.modifytitle = false;
				this.modify = false;
				this.stopcontent = true;
				this.stopselect = false;
				this.showcode = false;
			},
			//修改
			detail() {
				this.addtitle = false;
				this.modifytitle = true;
				this.viewtitle = false;
				this.modify = true;
				this.stopcontent = false;
				this.stopselect = true;
				this.$axios.get(this.basic_url + '/api-user/users/currentMap', {}).then((res) => {
	    			this.adddeptForm.changeby = res.data.nickname;
	    			var date=new Date();
					this.adddeptForm.changedate = this.$moment(date).format("YYYY-MM-DD HH:mm:ss");
					console.log(this.adddeptForm);
					for(var key in this.adddeptForm){ 
						this.adddeptForm.hasOwnProperty('_expanded');
						this.adddeptForm.hasOwnProperty('_level');
						this.adddeptForm.hasOwnProperty('_parent');
						this.adddeptForm.hasOwnProperty('_show');
						delete this.adddeptForm._expanded;
						delete this.adddeptForm._level;
						delete this.adddeptForm._parent;
						delete this.adddeptForm._show;
						
					}
					console.log(111);
					console.log(this.adddeptForm);
//					console.log(JSON.stringify(this.adddeptForm));
					//深拷贝数据
					let obj = JSON.stringify(this.adddeptForm);
					console.log(obj);
        			this.ADDDEPTFORM = JSON.parse(obj);
					
				}).catch((err) => {
					this.$message({
						message: '网络错误，请重试1',
						type: 'error'
					});
				});
                 this.show = true;
			},
			//获取负责人数据
			getPerson(){
				this.requestData();
				this.dialogLeader = true;
			},
			SelChange(val) {
				this.selUser = val;
				console.log(val);
			},
			loadMore() {
				if(this.loadSign) {
					this.loadSign = false
					this.page.currentPage++
						if(this.page.currentPage > Math.ceil(this.page.totalCount / this.page.pageSize)) {
							return
						}
					setTimeout(() => {
						this.loadSign = true
					}, 1000)
					this.requestData()
					//console.log('到底了', this.page.currentPage)
				}
			},
			sizeChange(val) {
				this.page.pageSize = val;
				this.requestData();
			},
			currentChange(val) {
				this.page.currentPage = val;
				this.requestData();
			},
			judge(data) {
				//taxStatus 布尔值
				return data.enabled ? '活动' : '不活动'
			},
			//时间格式化  
			dateFormat(row, column) {
				var date = row[column.property];
				if(date == undefined) {
					return "";
				}
				return this.$moment(date).format("YYYY-MM-DD"); 
			},
			addleader(){
				var selData = this.selUser;
				if(selData.length == 0) {
					this.$message({
						message: '请您选择数据',
						type: 'warning'
					});
					return;
				} else if(selData.length > 1) {
					this.$message({
						message: '不可同时选择多条数据',
						type: 'warning'
					});
					return;
				} else {
					this.adddeptForm.leader = selData[0].id;
					this.adddeptForm.leaderName = selData[0].nickname;
					this.dialogLeader = false;
				}
			},
			//点击修订按钮
			modifyversion(){
				this.$refs.adddeptForm.validate((valid) => {
					if(valid) {
						var ADDDEPTFORM=JSON.stringify(this.ADDDEPTFORM); //父传的值
	 					var adddeptForm=JSON.stringify(this.adddeptForm);//新输入的值
					 	if(adddeptForm==ADDDEPTFORM){
					  	this.$message({
								message: '没有修改内容，不允许修订！',
								type: 'warning'
							});
							return false;
					   }else{
							var url = this.basic_url + '/api-user/depts/upgraded';
							this.$axios.post(url, this.adddeptForm).then((res) => {
								//resp_code == 0是后台返回的请求成功的信息
								if(res.data.resp_code == 0) {
									this.$message({
										message: '修订成功',
										type: 'success'
									});
									//重新加载数据
									this.$emit('request');
									this.show = false;
								}else{
								this.show = true;
								if(res.data.resp_code == 1) {
									//res.data.resp_msg!=''后台返回提示信息
									if( res.data.resp_msg!=''){
									 	this.$message({
											message: res.data.resp_msg,
											type: 'warning'
									 	});
									}else{
										this.$message({
											message:'相同数据不可重复修订！',
											type: 'warning'
										});
									}
								}
							}		
							}).catch((err) => {
								this.$message({
									message: '网络错误，请重试1',
									type: 'error'
								});
							});
						}
					} else {
						this.$message({
							message: '未填写完整，请填写',
							type: 'warning'
						});
					}
				});
			},

			//所属上级
			getDept() {
				var page = this.page.currentPage;
				var limit = this.page.pageSize;
				var url = this.basic_url + '/api-user/depts/treeMap';
				this.$axios.get(url, {
					// params: {
					// 	page: page,
					// 	limit: limit,
					// 	// type: type
					// },
				}).then((res) => {
					this.resourceData = res.data;
					this.dialogVisible = true;
				});
			},
			queding() {
				console.log(this.checkedNodes);
				this.getCheckedNodes();
				if(this.checkedNodes == undefined){
					this.$message({
						message:'请选择数据',
						type:'warning'
					})
				// }else if(this.checkedNodes.length > 1){
				// 	this.$message({
				// 		message:'不可选择多条数据',
				// 		type:'warning'
				// 	})
				}else{
					this.placetext = false;
					this.dialogVisible = false;				
					this.adddeptForm.pid = this.checkedNodes[0].id;
					this.adddeptForm.parent = this.checkedNodes[0].fullname;
				}				
			},
			getCheckedNodes() {
				this.checkedNodes = this.$refs.tree.getCheckedNodes()
			},
			
			//生成随机数函数
			rand(min,max) {
		        return Math.floor(Math.random()*(max-min))+min;
		    },
			getDEPT_TYPE() {//获取机构属性
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=DEPT_TYPE';
				this.$axios.get(url, {}).then((res) => {
					this.SelectDEPT_TYPE = res.data;
				}).catch(error => {
					console.log('请求失败');
				})
			},
			getsys_depttype() {//获取机构类型
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=sys_depttype';
				this.$axios.get(url, {}).then((res) => {
					this.Selectsys_depttype = res.data;
				}).catch(error => {
					console.log('请求失败');
				})
			},
			

			//点击关闭按钮
			close() {
				this.show = false;
				this.$emit('request');
			},
			toggle(e) {
				if(this.isok1 == true) {
					this.maxDialog();
				} else {
					this.rebackDialog();
				}
			},
			maxDialog(e) { //定义大弹出框一个默认大小
				this.isok1 = false;
				this.isok2 = true;
				$(".mask_div").width(document.body.clientWidth);
				$(".mask_div").height(document.body.clientHeight - 60);
				$(".mask_div").css("top", "60px");
			},
			//还原按钮
			rebackDialog() { //大弹出框还原成默认大小
				this.isok1 = true;
				this.isok2 = false;
				$(".mask_div").css("width", "80%");
				$(".mask_div").css("height", "80%");
				$(".mask_div").css("top", "100px");
			},
			//获取负责人数据
			requestData(index) {
				var data = {
					page: this.page.currentPage,
					limit: this.page.pageSize,
					// deptName: this.searchList.deptName,
					// nickname: this.searchList.nickname,
					// username: this.searchList.username,
					companyId: this.companyId,
					deptId: this.deptId
				}
				var url = this.basic_url + '/api-user/users';
				this.$axios.get(url, {
					params: data
				}).then((res) => {
					this.page.totalCount = res.data.count;
					//总的页数
					let totalPage = Math.ceil(this.page.totalCount / this.page.pageSize)
					if(this.page.currentPage >= totalPage) {
						this.loadSign = false
					} else {
						this.loadSign = true
					}
					this.commentArr[this.page.currentPage] = res.data.data
					let newarr = []
					for(var i = 1; i <= totalPage; i++) {

						if(typeof(this.commentArr[i]) != 'undefined' && this.commentArr[i].length > 0) {

							for(var j = 0; j < this.commentArr[i].length; j++) {
								newarr.push(this.commentArr[i][j])
							}
						}
					}

					this.userList = newarr;
				}).catch((wrong) => {})
			},
			//保存
			save() {
				var _this = this;
				this.$refs.adddeptForm.validate((valid) => {
		          if (valid) {
		          	_this.adddeptForm.status=((_this.adddeptForm.status=="1"||_this.adddeptForm.status=='活动') ? '1' : '0');
//		          	 _this.adddeptForm.leader = _this.selData[0].id;
		          
					var url = _this.basic_url + '/api-user/depts/saveOrUpdate';

					console.log(_this.adddeptForm);
					this.$axios.post(url, _this.adddeptForm).then((res) => {
						//resp_code == 0是后台返回的请求成功的信息
						if(res.data.resp_code == 0) {
							this.$message({
								message: '保存成功',
								type: 'success'
							});
							//重新加载数据
							this.$emit('request');
//							 this.$refs["adddeptForm"].resetFields();//清空验证
						}
					}).catch((err) => {
						this.$message({
							message: '网络错误，请重试',
							type: 'error'
						});
					});
					this.falg=true;
		          } else {
						this.show = true;
						this.$message({
							message: '未填写完整，请填写',
							type: 'warning'
						});
						this.falg = false;
					}
		        });
				
			},
			saveAndUpdate(){
				this.save();
				if(this.falg){
					this.show = false;
				}
				this.$emit('request');
			},
			saveAndSubmit(){
				this.save();
				this.$emit('reset');
				this.$emit('request');
			},
			handleClose(done) {
				this.$confirm('确认关闭？')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			}
		},
		mounted() {
			this.getDEPT_TYPE();//页面打开加载-机构属性
			this.getsys_depttype();//页面打开加载-机构类型
			this.requestData();
		}
	}
</script>

<style>
	@import '../../assets/css/mask-modules.css';
	
	/*.el-form-item__error {
		top: 18%;
	    left: 5px;
	    background: #FFF;
	    padding: 5px 10px;
	}*/
</style>


